<template>
  <div>
    <app-header></app-header>
    <div class="vished-wrap w">
      <h2>Places Vished</h2>
      <h5>since {{dateFormat(new Date())}}</h5>
      <ul class="vished-ul">
        <li class="vished-ul-li" v-for="(item,index) in contacts" :key="index" @click="toDetail(index)">
          <div class="vished-ul-li-title">
              <span>{{item.locationInfo.name}}</span>
              <span class="vished-ul-li-number">{{item.contact.length}}</span>
          </div>
          <p>Last time visited: {{dateFormat(item.contact[item.contact.length-1].time)}}</p>
        </li>
      </ul>
      <p>Total number of places: {{contacts.length}}</p>
      <p>Total number of people met: {{totalPeoples}}</p>
    </div>
    <app-nav></app-nav>
  </div>
</template>

<script scoped>
import {getContacts, dateFormat} from '@/api/firebase'
export default {
  components: {
    'app-nav': () => import('@/components/nav'),
    'app-header': () => import('@/components/header')
  },
  methods: {
    toDetail (id) {
      this.$router.push({
        path: '/vished/detail/' + id
      })
    },
    dateFormat (date) {
      return dateFormat(Date(date), true)
    }
  },
  created () {
    getContacts().then(res => {
      this.contacts = res
      console.log(res)
    })
  },
  data () {
    return {
      contacts: []
    }
  },
  computed: {
    totalPeoples () {
      let sum = 0
      this.contacts.forEach(item => {
        sum += item.contact.length
      })
      return sum
    }
  }
}
</script>

<style>
.vished-ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.vished-ul-li {
  border-bottom: 1px solid #e5e5e5;
  margin: 10px 0;
}

.vished-ul-li .vished-ul-li-title {
  font-weight: bold;
  display: flex;
  justify-content: space-between;
}

.vished-ul-li-number{
    font-size: 25px;
}
</style>



